import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from '../public/account/login.component';
import { SignupComponent } from '../public/account/signup.component';
import { PublicLayoutComponent } from './public.layout.component';
import { StoreComponent } from './store/store.component';
import { UnauthorizeComponent } from './unauthorize/unauthorize.component';
import { CartComponent } from './cart/cart.component';


const publicroutes: Routes = [
  {
    path: '', component: PublicLayoutComponent, children: [
      { path: '', component: StoreComponent },
      { path: 'cart', component: CartComponent },
      { path: 'login', component: LoginComponent },
      { path: 'signup', component: SignupComponent },
      {path: 'unauthorize', component:UnauthorizeComponent}
    ]
  }
];

@NgModule({
  imports: [
    CommonModule, RouterModule.forChild(publicroutes)
  ],
  declarations: [],
  exports: [RouterModule]
})
export class PublicRoutingModule { }
export const PublicRoutingComponents = [LoginComponent, SignupComponent, 
  PublicLayoutComponent, StoreComponent, UnauthorizeComponent];
