import { Component, OnInit } from '@angular/core';
import { CartService } from '../services/cart.service';
import { AuthenticationService } from 'src/app/services/authentication.service';
import { Router } from '@angular/router';
import { StoreService } from 'src/app/services/store.service';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styles: []
})
export class CartComponent implements OnInit {

  constructor(public _cart:CartService, 
    private _authservice:AuthenticationService, 
    private _router: Router,
    private _storeservice:StoreService) { }

  ngOnInit() {
  }

  checkout(){
    if(this._authservice.user.isAuth){
      this._cart.userId = this._authservice.user.userId;
      this._storeservice.savecart(this._cart).subscribe((res:any) => {
        console.log(res);
        if(res.id != undefined){
         this._cart.checkoutPayUmoney(res.id, this._authservice.user);
        }
      });

    }else{
      this._router.navigate(['login'],{queryParams:{ref:'cart'}})
    }
  }

}
